import s from './Messages.module.css'
import DialogPerson from './messageComponents/DialogPerson'
import MessageBlock from './messageComponents/MessageBlock'
import MessageBlockUser from './messageComponents/MessageBlockUser'
import React, { useState } from 'react'

const Messages = (props) => {

    let messageBlocks = props.messages.map((el) => {
        return (
            <MessageBlock id={el.id} message={el.message} className={s.message}/>
        )
    })

    let dialogPersons = props.persons.map((el) => {
        return (
            <DialogPerson id={el.id} name={el.name} />
        )
    })

    const newMessageElement = React.createRef();

    const [messageWriter, setMessageWriter] = useState(messageBlocks)
    
    const newMessageCLick = () => {
        let newMessage = newMessageElement.current.value;
        setMessageWriter((messageBlocks) => [...messageBlocks, <MessageBlockUser id={10} message={newMessage}/>])
    }

    return (
        <div className={s.dialogs}>
            <div className={s.dialogItem}>
                { dialogPersons }
            </div>
            <div className={s.messages}>
                { messageWriter }
                <div className={s.newPost}>
                    <textarea ref={newMessageElement}></textarea>
                    <button style={{width: 100, height: 30, fontSize: 18}} onClick={newMessageCLick} >Send</button>
                </div>
            </div>
        </div>
    )
}

export default Messages;